import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BroadcastSetupComponent } from './components/broadcast-setup/broadcast-setup.component';
import { BroadcastHistoryComponent } from './components/broadcast-history/broadcast-history.component';
import { BroadcastSendmessageComponent } from './components/broadcast-sendmessage/broadcast-sendmessage.component';

const routes: Routes = [
  { path: '', redirectTo: 'setup', pathMatch: 'full'},
  { path: 'setup', component: BroadcastSetupComponent},
  { path: 'sendmessage', component: BroadcastSendmessageComponent},
  { path: 'history', component: BroadcastHistoryComponent},
  { path: '**', redirectTo: 'setup', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BroadcastRoutingModule { }
