import { Component, OnInit } from '@angular/core';

import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { BroadcastService } from '../../../../api/services/broadcast.service';

import * as _ from 'underscore';
@Component({
  selector: 'app-broadcast-sendmessage',
  templateUrl: './broadcast-sendmessage.component.html',
  styleUrls: ['./broadcast-sendmessage.component.scss']
})
export class BroadcastSendmessageComponent implements OnInit {

  message:any;
  allcategory:any;

  broadcastMessageGroup={
    "broadcastMessageGroupId": 0,
    "apartmentId": parseInt(this.cookieService.get('apartmentId')),
    "broadcastId": 0,
    "broadcastGroupId": 0,
    "staffCategoryIds": "string",
    "apartmentBlockIds": "string",
    "userIds": "string",
    "comment": "string",
    "isActive": true,
    "insertedBy": parseInt(this.cookieService.get('userId')),
    "insertedOn": new Date().toISOString(),
    "updatedBy": null,
    "updatedOn": "2020-03-28T01:04:37.407Z"
  }
  allBroadcastMessageGroup = [];

  isFlashNotice:boolean=false;

  editorPlacehorder:string = "";

  constructor(
    public broadcastService: BroadcastService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  onContentChanged(event){
    
  }

  ngOnInit() {
    this.message={};
    this.getAllCategory();
  }

  flashNotice(bool){
    debugger;
    this.isFlashNotice = bool;
  }
  categoryChange(value){
    let id = parseInt(value);
    var data = _.filter( this.allcategory, function( item ) {
      if(item.broadCastCategoryId === id)
        return item;
      } );

      if(data === undefined || data === null || data.length == 0){
        this.message.ckeditor = '';
      }
      else {
        this.message.ckeditor = data[0].template;
      }
  }

  //Get All Broadcast Category
  getAllCategory(){
    let apartmentID = parseInt(this.cookieService.get('apartmentId'));
    this.broadcastService.getBroadCastCategorysByApartmentId(apartmentID).subscribe((res:any) => {
      var allcategory = res;
      //filter active true items
      this.allcategory = allcategory.filter(data => {
        return data.isActive
      })
    })
  }

  //Submit Category
  submitCreateBroadcastMessageForm(form){
      
      let broadcastMessage={
          "broadcastId": 0,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "subject": this.message.subject,
          "broadcastMessage": this.message.ckeditor,
          "broadcastBy": parseInt(this.cookieService.get('userId')),
          "broadcastOn": new Date().toISOString(),
          "description": "string",
          "broadcastCategoryId": this.message.category,
          "broadcastTypeId": 0,
          "broadcastModeId": 0,
          "broadcastGroupId": 0,
          "parentId": 0,
          "isLater": true,
          "laterDate": null,
          "isFlashNotice": this.isFlashNotice,
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": null,
          "updatedOn": null,
          "broadcastMessageGroup": []
        }
      this.broadcastService.addBroadcast(broadcastMessage).subscribe((res:any) => {
        if(res.message) {
          this.sharedService.setAlertMessage("Broadcast Message sent successfully");
          this.message = {};
        }
      });

  }

}
