import { Component, OnInit, Injector } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { BroadcastService } from '../../../../api/services/broadcast.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-broadcast-setup',
  templateUrl: './broadcast-setup.component.html',
  styleUrls: ['./broadcast-setup.component.scss']
})
export class BroadcastSetupComponent implements OnInit {

  category:any;

  isDataLoaded:boolean = false;

  bcData:string = "";
  modalService:any;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;
  totalFilterItems:any;

  isEdit:boolean = false;

  //Get all work type
  allBroadcastCategory:any;

  errorMessage:string = "";
  isError:boolean = false;

  editorPlacehorder;string = "";

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
  	private broadcastService: BroadcastService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
   }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  onContentChanged(event){
    
  }

  ngOnInit() {
    this.category = {};
    this.getAllCategory();

    // delete category
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {
      if(index != null){
        var params = {
          broadcastId: this.allBroadcastCategory[index].broadCastCategoryId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }
        this.isDataLoaded = false;
        this.broadcastService.deleteBroadCastCategory(params).subscribe((res:any) => {
          this.allBroadcastCategory.splice(index, 1);
          this.totalItems = this.allBroadcastCategory.length;
          this.isDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);
        }, error => {
          console.log(error);
        });
      }
    });
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  //Update Data
  updateData(data){
    this.category = data;
    this.isEdit = true;
  }

  //Get Category Type
  getAllCategory(){
    let apartmentID = parseInt(this.cookieService.get('apartmentId'));
    this.broadcastService.getBroadCastCategorysByApartmentId(apartmentID).subscribe((res:any) => {
      var allBroadcastCategory = res;
      //filter active true items
      this.allBroadcastCategory = allBroadcastCategory.filter(data => {
        return data.isActive
      })
      this.isDataLoaded = true;
      this.totalItems = this.allBroadcastCategory.length;
      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }
    })
  }

  //Submit Category
  submitCreateBroadcastCategoryForm(form){

    if(this.isEdit==false){
      
      let broadcastCategory={
        "broadCastCategoryId": 0,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "broadCastCategory1": this.category.broadCastCategory1,
        "template":  this.category.template,
        "comments": "string",
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": null,
        "updatedOn": null
      }
      this.broadcastService.addBroadCastCategory(broadcastCategory).subscribe((res:any) => {
        if(res.message) {
          this.sharedService.setAlertMessage("Broadcast category added successfully");
          this.getAllCategory();
          this.category = {};
        }
      });

    }else{

      let broadcastCategory={
        "broadCastCategoryId": this.category.broadCastCategoryId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "broadCastCategory1": this.category.broadCastCategory1,
        "template":  this.category.template,
        "comments": "string",
        "isActive": true,
        "insertedBy": this.category.insertedBy,
        "insertedOn": this.category.insertedOn,
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()
      }
      this.broadcastService.updateBroadCastCategory(broadcastCategory).subscribe((res:any) => {
        if(res.message) {
          this.sharedService.setAlertMessage("Broadcast category updated successfully");
          this.getAllCategory();
          this.category = {};
          this.isEdit = false;
        }
      });

    }

  }

}
